import styled from 'styled-components'

export default styled.div`
    background-color: ${({ bg }) => (bg && bg) || 'unset'};
    display: flex;
    flex-direction: ${({ horizontal }) => (horizontal && 'row') || 'column'};
    justify-content: center;
    align-items: center;
    padding: 8px;
    color: ${({ color }) => (color && color) || 'unset'};
`
