import styled from 'styled-components'

export default styled.div`
    min-height: 300px;
    display: flex;
    align-items: flex-end;

    footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 32px;
        color: white;
        background-color: #1e272e;

        @media (min-width: 600px) {
            flex-direction: row;
        }
    }
`
