import React from 'react'
import Header from './Header'
import Container from './styles'
import Center from './Center'
import Grid from './Grid'
import Document from './Document'
import Footer from './Footer'
import {
    DiReact,
    DiPython,
    DiPhp,
    DiUnitySmall,
    DiDebian,
    DiDocker,
    DiMongodb,
    DiMysql,
    DiJsBadge,
    DiNodejs,
    DiHeroku,
    DiAndroid,
    DiGithubBadge
} from 'react-icons/di'
import { IoIosAppstore, IoIosApps } from 'react-icons/io'
import { FaApple, FaGooglePlay } from 'react-icons/fa'

const ico = {
    size: 64,
    style: {
        margin: 8,
        color: 'grey'
    }
}
const logos = {
    style: {
        width: 128,
        height: 128
    }
}
export default () => {
    return (
        <>
            <Container>
                <Header />

                <Center color="white" bg="#1e272e">
                    <h3>O que eu manjo?</h3>
                </Center>
                <Grid>
                    <Center>
                        <h3>Linguagens</h3>
                        <Grid>
                            <DiJsBadge {...ico} />
                            <DiPython {...ico} />
                            <DiPhp {...ico} />
                        </Grid>
                    </Center>
                    <Center>
                        <h3>Plataformas</h3>
                        <Grid>
                            <DiNodejs {...ico} />
                            <DiDocker {...ico} />
                            <DiGithubBadge {...ico} />
                            <DiHeroku {...ico} />
                        </Grid>
                    </Center>
                    <Center>
                        <h3>Banco de dados</h3>
                        <Grid>
                            <DiMongodb {...ico} />
                            <DiMysql {...ico} />
                        </Grid>
                    </Center>

                    <Center>
                        <h3>Outros</h3>
                        <Grid>
                            <DiReact {...ico} />

                            <DiUnitySmall {...ico} />
                            <DiDebian {...ico} />

                            <DiAndroid {...ico} />
                        </Grid>
                    </Center>
                </Grid>
                <Center color="white" bg="#1e272e">
                    <h3>O que estou fazendo ultimamente?</h3>
                </Center>
                <Center>
                    <Document>
                        <p>
                            No ano de <strong>2019</strong>, venho desenvolvendo
                            produtos internos para a RPS Capital.
                        </p>
                        <p>Aplicações Android, Web, APIs, Softwares, etc</p>
                    </Document>
                </Center>
                <Center color="white" bg="#1e272e">
                    <h3>Meus projetos</h3>
                </Center>
                <Center>
                    <img
                        {...logos}
                        src={require('../../media/angoole-logo.png')}
                    />

                    <Document>
                        <p>
                            Um bot do messenger que realiza pesquisas no Google
                            e envia uma imagem da página selecionada.
                        </p>
                        <p>
                            O bot foi desenvolvido em Node JS com Express para
                            requisições HTTP e o Puppeteer para abrir páginas e
                            tirar screenshot. Atualmente ele está rodando em um
                            container do Docker hospedado na Heroku.
                        </p>
                    </Document>
                    <Center horizontal>
                        <a
                            target="_blank"
                            href="https://www.facebook.com/Angoole-102519461116556"
                        >
                            Acessar
                        </a>
                        <a
                            target="_blank"
                            href="https://github.com/WilianZilv/angoole"
                        >
                            <div>
                                <DiGithubBadge />
                                Github
                            </div>
                        </a>
                    </Center>
                </Center>
                <Center>
                    <img
                        {...logos}
                        src={require('../../media/raffa-moreira-logo.png')}
                    />
                    <h4>Raffa Moreira Simulator</h4>
                    <br />
                    <Document>
                        <p>
                            Jogo desenvolvido na Unity. A ideia surgiu como uma
                            brincadeira em uma conversa aleatória com um amigo.
                            Após o brainstorm, desenvolvi em 2 dias, explodiu em
                            julho de 2018, no mesmo dia em que divulguei
                        </p>
                    </Document>
                    <Center horizontal>
                        <a
                            target="_blank"
                            href="https://apps.apple.com/pt/app/raffa-moreira-simulator/id1422422270"
                        >
                            <div>
                                <FaApple />
                                AppStore
                            </div>
                        </a>
                        <a
                            target="_blank"
                            href="https://play.google.com/store/apps/details?id=com.gomi.raffamoreirasimulator"
                        >
                            <div>
                                <FaGooglePlay />
                                PlayStore
                            </div>
                        </a>
                    </Center>
                </Center>
            </Container>
            <Footer />
        </>
    )
}
