const icon = {
    style: {
        fontSize: 32,
        margin: 8,
        color: 'white'
    }
}
export default () => {
    return (
        <Container>
            <footer>
                <a target="_blank" href="https://www.facebook.com/wilianzilv">
                    <IoLogoFacebook {...icon} />
                </a>
                <a target="_blank" href="https://github.com/WilianZilv">
                    <DiGithubBadge {...icon} />
                </a>
                <FaDiscord {...icon} /> WilianZilv#8934
                <IoMdMail {...icon} /> wilianzilv@gmail.com
            </footer>
        </Container>
    )
}
