import React, { useState, useEffect } from 'react'
import Container from './styles'

const calc = new Date(Date.now()).getFullYear() - 1998

export default () => {
    const [age, setAge] = useState('new Date(Date.now()).getFullYear() - 1998')

    const Code = () => (
        <>
            <br />
            <code>{age}</code>
            <br />
        </>
    )

    useEffect(() => {
        setTimeout(() => {
            setAge(calc)
        }, 1000)
    }, [])
    return (
        <Container horizontal bg={'#485460'}>
            <img src={require('../../../media/me-eating.jpg')} />
            <div>
                <h2>Esse sou eu, me chamo Wilian</h2>
                <h5>
                    Tenho {(Number.isInteger(age) && age) || <Code />} anos e
                    atualmente moro em Curitiba
                </h5>
            </div>
        </Container>
    )
}
