import React from 'react'
import GlobalStyle from './styles/global'
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import { Home } from './routes'

export default () => {
    return (
        <>
            <BrowserRouter>
                <Switch>
                    <Route path="/" exact component={Home} />
                    <Redirect from="*" to="/" />
                </Switch>
            </BrowserRouter>
            <GlobalStyle />
        </>
    )
}
