import styled from 'styled-components'

export default styled.div`
    footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
    }
    a {
        all: unset;
        font-family: 'Manjari', sans-serif;
        cursor: pointer;
        margin: 8px;
        padding: 4px 8px;
        background: #2ecc71;
        border-radius: 2px;
        color: white;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
        div {
            display: flex;
            align-items: center;
            font-family: 'Manjari', sans-serif;
        }
    }
`
